.footer-title {
    display: block;
    color: $white;
    margin-bottom: 10px;
    font-weight: bold;
    font-style: 18px;

    @include media-breakpoint-up(md) {
        margin-bottom: 30px;
    }
}

.footer-copyright {
    font-size: 12px;
    line-height: 20px;
    font-weight: 300;
    color: #969696;
    margin-bottom: 30px;

    @include media-breakpoint-up(md) {
        margin-top: 65px;
    }
}

.footer-info {
    font-size: 12px;
    line-height: 20px;
    font-weight: 300;
    margin-bottom: 20px;
}

.footer-bottom {
    margin-top: 44px;

    @include media-breakpoint-up(md) {
        margin-top: 74px;
    }
}

.footer-mail {
    display: inline-block;
    margin-top: 20px;
}
