.exchange-wrap {
    padding-top: 45px;
    padding-bottom: 45px;
    background-image: url('../img/bg.png');

    @include media-breakpoint-up(md) {
        padding-top: 120px;
        padding-bottom: 120px;
    }
}

.exchange {
    display: flex;
    align-items: center;
    justify-content: center;
}

.exchange-form {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;

    @include media-breakpoint-up(xl) {
        flex-wrap: nowrap;
    }

    &__item {
        position: relative;
        width: 100%;
        margin-bottom: 1rem;

        @include media-breakpoint-up(lg) {
            width: 157px;
        }

        @include media-breakpoint-up(xl) {
            margin-bottom: 0;
        }

        &--xl {
            @include media-breakpoint-up(xl) {
                width: 270px;
            }
        }

        .form-control,
        .custom-select {
            position: relative;
            border: none;
            width: 100%;
            border-radius: $border-radius-lg;

            @include media-breakpoint-up(lg) {
                border-radius: 0;
            }
        }

        &:first-child::before {
            content: none;
        }

        &:last-of-type {
            @include media-breakpoint-up(xl) {
                background-color: $white;
                width: 180px;
                position: relative;

                &::after {
                    position: absolute;
                    content: '';
                    padding: 20px;
                    top: 0;
                    bottom: 0;
                    left: 100%;
                    background-color: $white;
                }
            }
        }

        &:first-child .form-control,
        &:first-child .custom-select {
            border-top-left-radius: $border-radius-lg;
            border-bottom-left-radius: $border-radius-lg;
        }

        &:last-of-type .form-control,
        &:last-of-type .custom-select {
            border-top-right-radius: $border-radius-lg;
            border-bottom-right-radius: $border-radius-lg;

            @include media-breakpoint-up(xl) {
                border-radius: 0;
            }
        }

        &::before {
            @include media-breakpoint-up(lg) {
                content: '';
                position: absolute;
                left: 0;
                top: $input-btn-padding-y-lg;
                bottom: $input-btn-padding-y-lg;
                border: 1px solid #c8c8c8;
                z-index: 1;
            }
        }
    }

    &__btn {
        z-index: 1;
    }

    .custom-select-img {
        padding-right: 20px;
    }
}
