.header-nav {
    padding-top: 20px;

    @include media-breakpoint-up(md) {
        padding-top: 60px;
    }

    .navbar-nav {
        margin-top: 20px;
        @include media-breakpoint-up(lg) {
            margin-top: 0;
        }
    }

    .navbar-nav .nav-item {
        margin-bottom: 10px;
        @include media-breakpoint-up(lg) {
            margin-left: 22px;
            display: flex;
            align-items: center;
        }
    }

    .navbar-nav .nav-item--sign {
        margin-left: 50px;
        margin-right: 15px;
    }


    .navbar-brand {
        font-family: Tahoma;
        font-weight: bold;
    }

    .dropdown-menu {
        min-width: 4rem;
    }
}
