.advantages {
    padding-top: 45px;
    color: #ffffff;
    background-image: linear-gradient(45deg, #0a9976 0%, #189c5c 100%);

    @include media-breakpoint-up(md) {
        padding-top: 114px;
        padding-bottom: 73px;
    }
}

.advantages-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 45px;
    text-align: center;
    font-size: 17px;
    line-height: 27px;

    &__bg {
        width: 150px;
        height: 100px;
        padding-top: 10px;
        background-image: url('/img/checked.png');
        background-repeat: no-repeat;
        background-size: 60px 60px;
        background-position: top right;
        @include media-breakpoint-up(md) {
            margin-bottom: 14px;
        }
    }

    &__title {
        word-break: break-word;
        padding: 0 5px;
        width: 100%;
    }
}
