.socic-list {
    margin-top: 25px;
    white-space: nowrap;
    
    &__item {
        margin-right: 28px;
        margin-bottom: 10px;

        &:last-child {
            margin-right: 0;
        }

        &:hover,
        &:focus,
        .active {
            text-decoration: none;
            .socic-list__img {
                fill: $green;
            }
        }
    }
}
