.header-info {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    padding-top: 45px;
    padding-bottom: 45px;
    color: $white;
    font-size: 20px;

    @include media-breakpoint-up(md) {
        padding-top: 172px;
        padding-bottom: 180px;
        font-size: 25px;
    }

    &__header {
        margin-bottom: 25px;
        font-weight: 600;
        width: 100%;

        @include media-breakpoint-up(md) {
            margin-bottom: 50px;
        }
    }

    &__title {
        width: 100%;
    }

    .btn-default {
        margin-top: 35px;

        @include media-breakpoint-up(md) {
            margin-top: 73px;
        }
    }
}
