.page-header {
    flex-shrink: 0;
    background: url('../img/header-bg.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.page-main {
    min-height: 350px;
    flex: 1 0 auto;
}

.page-footer {
    padding-top: 45px;
    padding-bottom: 45px;
    flex-shrink: 0;
    background: $black;
    color: $gray-default;

    @include media-breakpoint-up(md) {
        padding-top: 104px;
    }

    .custom-list {
        font-size: 15px;
    }
}
